.language-slider {
  padding: 40px;
  background-color: #f0f8ff; /* Light blue background */
  animation: fadeIn 1s ease-in-out;
}

.slider {
  display: flex;
  overflow-x: scroll;
  gap: 20px;
  padding: 20px 0;
  scrollbar-width: thin; /* For Firefox */
}

.slider::-webkit-scrollbar {
  height: 8px;
}

.slider::-webkit-scrollbar-thumb {
  background-color: #888;
  border-radius: 10px;
}

.language-card {
  min-width: 200px; /* Responsive size */
  max-width: 300px; /* Responsive size */
  cursor: pointer;
  text-align: center;
  transition: transform 0.3s ease;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  padding: 20px;
  flex: 1 1 250px; /* Flex properties for responsiveness */
}

.language-card:hover {
  transform: scale(1.05);
}

.language-image {
  width: 100%;
  height: 150px; 
  border-radius: 10px;
  object-fit: cover;
  margin-bottom: 10px;
}

.lang-popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.5);
  z-index: 5;
}

.lang-popup-content {
  background: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  max-width: 540px;
  text-align: center;
}

.lang-popup-content h3 {
  margin-top: 0;
}

.popup-content button {
  background-color: #228B22; /* Forest Green */
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 1em;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.popup-content button:hover {
  background-color: #1B5E20; /* Darker Green */
}
